@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,800;1,900&display=swap);

header{
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    color: #000000;
    padding-top: 2%;
    padding-bottom: 2%;
    grid-area: header;
}

header > p{
    font-weight: 900;
    width: 100%;
    padding-left: 5%;
}

nav{
    position: fixed;
    margin-top: 5%;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    margin-left: 0;
}

nav a{
    color: #000000;
    text-decoration: none;
    padding-left: 5%;
    margin-top: 5%;
    margin-bottom: 5%;
}

.active{
    color: #5a80ef;
}
html{
  font-family: 'Raleway', sans-serif!important;
  scroll-behavior: smooth;
}

body{
  margin: 0;
}

main{
  grid-area: main;
}

.App{
  display: grid;
  grid-template-columns: 10% auto;
  grid-template-rows: 100vh;
  grid-template-areas: "header main";
}

.pagegarde{
  padding-left: 2%;
  padding-right: 2%;  
  height: 100vh;
  background-color: #1E2046;
  background-size: 125% 125%;
  color: #ffffff;
}

.wrapper{
  max-width: 1000px;
  margin: auto;
  padding-top: 10%;
}

.wrapper > *{
  margin: 0;
}

h1{
  font-size: 60px;
  font-weight: 800;
}

.wrapper h2{
  font-size: 70px;
  margin-top: -20px;
  background: linear-gradient(90deg, #5a80ef, #34d5b0);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

.wrapper h3{
  margin-top: 25px;
  font-size: 25px;
  font-weight: 600;
}

.wrapper > p{
  margin-top: 15px;
}

.sombre a:not(.next){
  color: #34d5b0;
}

a{
  text-decoration: none;
  color: #5a80ef;
}

.next{
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
  color: #ffffff;
  height: 50px;
  width: 200px;
  background: linear-gradient(90deg, #5a80ef, #34d5b0);
  margin-top: 50px;
  font-weight: 600;
}

.next:hover{
  background: #2C416F;
}

section{
  padding-left: 2%;
  padding-right: 2%;
  background: #F6FDFF;
}

.vh{
  height: 100vh;
}

.wrapper-section{
  padding-top: 2%;
}

.h2{
  margin-top: 0;
  font-size: 40px;
  font-weight: 800;
  background: linear-gradient(90deg, #5a80ef, #34d5b0);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

span{
  font-weight: bold;
}

.sombre{
  background: #1E2046;
  color : #ffffff;
}

.sombre p{
  font-weight: 100;
  font-size: 20px;
}

.img{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.legend{
  font-style: italic;
}

.wrapper-annexes{
  display: flex;
  flex-direction: column;
}

.wrapper-annexes img{
  margin: auto;
  max-width: 80%;
}

.wrapper-annexes p{
  margin: auto;
  margin-top: 20px;
}
